import { LoginheaderService } from '../../../services/loginheader.service';
import { Component, OnInit, Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { CartCountService } from 'src/app/services/cart-count.service';
import { ToastrService } from 'ngx-toastr';
import gql from 'graphql-tag';
import { Apollo, Mutation } from 'apollo-angular';
import { NgxUiLoaderService } from 'ngx-ui-loader';



@Injectable({
  providedIn: 'root',
})
export class UpvotePostGQL extends Mutation {
  document =  gql`
  mutation addToCart($cartItemModel: CartItemModel!) {
    addToCart(cartItemModel:$cartItemModel){
      id          
    }
  }
`;


}

@Component({
  selector: 'app-product-details',
  templateUrl: './product-details.component.html',
  styleUrls: ['./product-details.component.scss']
})
export class ProductDetailsComponent implements OnInit {
selectedProducts:any ;
  constructor(
    public router: Router,
    private cartCount:CartCountService,
    private toastr: ToastrService,
    private apollo:Apollo, 
    private getCartitem : UpvotePostGQL,
    private ngxService: NgxUiLoaderService,

    ) { 
    const data = history.state.data;
    this.selectedProducts = data;
    console.log('history data is ', data)
    // this.selectedProducts = 
  }

  ngOnInit() {
  }

  async addtoCart(){
    this.ngxService.start();
  //selectedProducts here is my  product
  let result = await this.addCart()
  console.log('result isss', result);
  if(result){
    this.ngxService.stop();

    this.toastr.success('Added to cart')
    this.cartCount.broadCastMessage('cart added');
    console.log(this.selectedProducts);  
  }  




  }


  public addCart = () => {
    return new Promise((res,rej)=>{
      
      let cartItemModel = {
        userId:"5ef21c111632ecc321bebe70",
        productId:"5ed8fc1fd75df774b2160d5a",
        quantity:1,
        totalAmount:20,
        amountToBePaid:20,
        offerId:null
      }
    //send the query to server

    this.getCartitem
    .mutate({cartItemModel})
    .subscribe(({data}) => {
     res(data);
     console.log('mutation',data)
    },
    (err)=>{
      rej(err);
    });
        })
  }
}


