import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {MatToolbarModule} from '@angular/material/toolbar';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { LoginComponent } from './auth-comp/login/login.component';
import { RegisterComponent } from './auth-comp/register/register.component';
import { HomeComponent } from './dashboard/home/home.component';
import { ProductDetailsComponent } from './dashboard/home/product-details/product-details.component';
import { HeaderComponent } from './dashboard/home/header/header.component';
import { AllProductsComponent } from './dashboard/home/all-products/all-products.component';
import {MatButtonModule} from '@angular/material/button';
import {MatDialogModule} from '@angular/material/dialog';
import { AuthCompComponent } from './auth-comp/auth-comp.component';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import { OwlModule } from 'ngx-owl-carousel';
import { FooterComponent } from './dashboard/home/footer/footer.component';  
import {MatInputModule} from '@angular/material/input';
import {MatBadgeModule} from '@angular/material/badge';
import { MatIconModule } from '@angular/material/icon'
import {MatMenuModule} from '@angular/material/menu';
import { CartComponent } from './dashboard/home/header/cart/cart.component';
import { FormsModule } from '@angular/forms';
import { ProductIntroComponent } from './dashboard/home/product-intro/product-intro.component';
import { UserProfileComponent } from './dashboard/home/user-profile/user-profile.component';
import { GraphQLModule } from './graphql.module';
import { HttpClientModule } from '@angular/common/http';
import { ToastrModule } from 'ngx-toastr';
import { NgxUiLoaderModule } from 'ngx-ui-loader';
import { StoreService } from './services/store-service.service';
import { LoginheaderService } from './services/loginheader.service';
import { AgeConfirmationComponent } from './dashboard/home/age-confirmation/age-confirmation.component';

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    RegisterComponent,
    HomeComponent,
    ProductDetailsComponent,
    HeaderComponent,
    AllProductsComponent,
    AuthCompComponent,
    FooterComponent,
    CartComponent,
    ProductIntroComponent,
    UserProfileComponent,
    AgeConfirmationComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    MatToolbarModule,
    MatInputModule,
    MatBadgeModule,
    MatIconModule,
    MatMenuModule,
    FormsModule,
    GraphQLModule,
    HttpClientModule,
    MatButtonModule,MatDialogModule,NgbModule,OwlModule  ,
    ToastrModule.forRoot() ,
    NgxUiLoaderModule,
  ],
  providers: [StoreService,LoginheaderService],
  entryComponents:[AuthCompComponent,AgeConfirmationComponent],
  bootstrap: [AppComponent]
})
export class AppModule { }
