import { Component, OnInit } from '@angular/core';
import { Apollo } from 'apollo-angular';
import gql from 'graphql-tag';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.scss']
})
export class CartComponent implements OnInit {
  cartCount:number = 1;
 
  constructor( private apollo:Apollo) { }

 async ngOnInit() {
    let user = JSON.parse(localStorage.getItem("user"));
    console.log('carttttt',user);
    
  let res = await  this.getCartForUser(user.login.id);
  console.log('res is ', res);
  
  this.cartCount = res["cartitems"].length;
  console.log('aaaaaaaa', this.cartCount);
  
  }



  public getCartForUser = (id) => {
    return new Promise((res,rej)=>{
      const cartitemsQuery = gql`
      query cartitems($userId: String!) {
        cartitems(userId:$userId){
          totalAmount
            product {
              name,
              price,
              fullDesc,
              images,
              tags


            }
        }
      }
    `;
   this.apollo
    .watchQuery({
      query: cartitemsQuery,
      variables: {
        userId:id
      },
    })
    .valueChanges.subscribe(({data}) => {
     res(data);
    },(err)=>{
      rej(err);
    });
        })
  }



  decreaseCart(){
    this.cartCount--;
  }
  increaseCart(){
    this.cartCount++;
  }

  placeOrder(){

  }
}
