import { DataServiceService } from './../../services/data-service.service';
import { Component, OnInit, Output, EventEmitter} from '@angular/core';
@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {
  isnewUser:boolean;
  @Output() userStatus: EventEmitter<any> = new EventEmitter<any>();

  constructor(private _data: DataServiceService) {
    this.isnewUser = this._data.isnewUser
   }

  ngOnInit() {
  }
  oldUser(){
    this._data.isnewUser = false;
    this.userStatus.emit(this._data.isnewUser);

  }
}
