import { UserProfileComponent } from './dashboard/home/user-profile/user-profile.component';
import { CartComponent } from './dashboard/home/header/cart/cart.component';
import { HomeComponent } from './dashboard/home/home.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ProductDetailsComponent } from './dashboard/home/product-details/product-details.component';

const routes: Routes = [
  {
    path:'',
    component:HomeComponent
  },
 {
   path:'product-details',
   component:ProductDetailsComponent
 },
 {
   path:'viewcart',
   component:CartComponent
 },
 {
   path:'profile',
   component:UserProfileComponent
 }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {onSameUrlNavigation: 'reload'})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
