import { AgeConfirmationComponent } from './../age-confirmation/age-confirmation.component';
import { LoginheaderService } from "./../../../services/loginheader.service";
import { Router } from "@angular/router";
import { DataServiceService } from "./../../../services/data-service.service";
import { Component, OnInit } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { AuthCompComponent } from "src/app/auth-comp/auth-comp.component";
import { ViewEncapsulation } from "@angular/core";
import { ToastrService } from "ngx-toastr";
import { NgxUiLoaderService } from "ngx-ui-loader"; // Import NgxUiLoaderService
import { Subscription } from "rxjs";
import { CartCountService } from 'src/app/services/cart-count.service';

@Component({
  selector: "app-header",
  templateUrl: "./header.component.html",
  styleUrls: ["./header.component.scss"],
  encapsulation: ViewEncapsulation.None,
})
export class HeaderComponent implements OnInit {
  isLoggedin: boolean;
  userName: string;
  cartCount: number = 0 ;
  subscription: Subscription;
  countSubscription: Subscription;
  //contains product in cart
  cartProduct:any;

  constructor(
    public dialog: MatDialog,
    private toastr: ToastrService,
    private ngxService: NgxUiLoaderService,
    private dataService: DataServiceService,
    private router: Router,
    private loginstatusService: LoginheaderService,
    private cartCountservice: CartCountService
  ) {
    // this.cartCount = this.dataService.cartCount;
  }

  ngOnInit() {

    let myData = JSON.parse(localStorage.getItem("user"));
    if (myData) {
      this.userName = myData.login.userProfile.firstName;
      console.log("from local storage i got user", myData);
      this.isLoggedin = JSON.parse(localStorage.getItem("isLoggedIn"));
      console.log("from local storage is login", this.isLoggedin);
    }

    // subscribe to home component messages
    this.subscription = this.loginstatusService
      .retriveMessage()
      .subscribe((message) => {
        if (message) {          
          this.isLoggedin = true;         
          myData = JSON.parse(localStorage.getItem("user"));
          if (myData) {
            this.userName = myData.login.userProfile.firstName;
          }
        } else {         
          this.isLoggedin = false;
        }
      });

      //cart count subscribe
      this.countSubscription = this.cartCountservice
      .retriveMessage()
      .subscribe((message) => {
          this.cartCount++;
      });

      //age dialog start
      // const agedialog = this.dialog.open(AgeConfirmationComponent, {
      //   width: "600px",
      //   height: "400px",
      //   data: {
     
      //   },
      // });
      //age dialog end

  }

  logmeIn() {
    console.log("log in");

    const dialogRef = this.dialog.open(AuthCompComponent, {
      width: "600px",
      height: "400px",
      data: {
        // model: {
        //   "annot_name": 'n',
        //   "annot_value":"v"
        // },
      },
    });
   

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);

      const myData = JSON.parse(localStorage.getItem("user"));
      console.log("from local storage i got user", myData);

      const isLog = JSON.parse(localStorage.getItem("isLoggedIn"));
      console.log("from local storage is login", isLog);
    });
  }

  logmeOut() {
    localStorage.removeItem("user");
    localStorage.removeItem("isLoggedIn");
    this.router.navigate(["/"]);

    this.isLoggedin = false;
  }
}
